import React, { Component } from 'react';
import { authx, rdbx, dbx } from "../firebase";
import ReactDOM from 'react-dom';
import moment from 'moment'
import { AppString } from "../const";

import { emojis } from "./min-component/emoji";


class Home extends Component {
    constructor(props) {
        super(props)
        this.state = {
            uid: null,
            name: null,
            messages: [],
            text: '',
            emoji: emojis,
            stallID: '',
            allStallAdmin: [],
            stallManagerAvatar: AppString.DEFAULT_AVATAR,//Default Image
            userAvatar: AppString.DEFAULT_AVATAR,//Default Image
            stallName: '',
            stallExternalName: '',
            stallUserID: ''
        }
        window.ReactStallManager = this;
    }

    //React Hook
    componentWillMount() {
        const cachedHits = localStorage.getItem('loginKey');
        if (cachedHits) {
            let cc = JSON.parse(cachedHits);
            let uid = cc.uid;
            let username = cc.username;
            let stall_id = cc.stallID;
            this.state.stallID = stall_id;
            this.setState({ uid: uid, name: username, stallID: stall_id })
            localStorage.removeItem("loginKey");
        } else {

            localStorage.removeItem("loginKey");
            this.props.isLogout();
            // this.props.isLogout()

        }

        //End LoginInfo

        let stall_Id = this.state.stallID;

        dbx.collection(AppString.ADMIN_DOC).doc(AppString.ADMIN_TABLE).get().then(fun => {
            let manager = fun.data();
            this.state.allStallAdmin = manager;
            //This is consider as default stall user
            for (let i = 0; i < manager.AdminList.length; i++) {

                if (manager.AdminList[i].stallName == stall_Id) {
                    console.log(manager.AdminList[i])
                    this.setState({ stallExternalName: manager.AdminList[i].externalName ? manager.AdminList[i].externalName : manager.AdminList[i].stallName })
                    this.setState({ stallName: manager.AdminList[i].stallName })
                    this.singleChat(manager.AdminList[i].adminUID);
                    break;
                }
            }
            //console.log(manager.AdminList);
            // this.singleChat(manager.managerID);
        });

    }

    componentDidUpdate() {
        this.scrollToBottom();
    }

    //Change Chat manager
    chatWithStallManager = (stallID) => {

        let count = this.state.allStallAdmin.AdminList.length;
        for (let i = 0; count; i++) {

            if (this.state.allStallAdmin.AdminList[i].stallName == stallID) {
                this.setState({ stallExternalName: this.state.allStallAdmin.AdminList[i].externalName ? this.state.allStallAdmin.AdminList[i].externalName : this.state.allStallAdmin.AdminList[i].stallName })
                this.setState({ stallName: this.state.allStallAdmin.AdminList[i].stallName })
                this.singleChat(this.state.allStallAdmin.AdminList[i].adminUID);
                break;
            }
        }

    }
    // window.ReactStallManager.chatWithStallManager('Stall A')
    //Select Single Person
    singleChat = (person) => {

        this.getUserAvatarImageByID(this.state.uid, 0); //Get Corrent user Image
        this.getUserAvatarImageByID(person, 1); //Get Stall Manager Imager

        this.setState({ messages: [] });
        let givenDB = this.state.uid + '+' + person;
        let fromDB = person + '+' + this.state.uid;
        this.setState({ dbName: givenDB });
        this.setState({ stallUserID: person });

        let msgs = [];
        const rootRef = rdbx.ref().child(this.state.stallName).child(givenDB);
        rootRef.on('child_added', snap => {
            if (snap.val()) {
                msgs.push({
                    uid: snap.val().uid,
                    time: snap.val().time,
                    name: snap.val().name,
                    msg: snap.val().text
                })
                this.setState({ messages: msgs })
            }
            console.log(msgs.length);
        });
        let tempArray = [];

        const rootRef2 = rdbx.ref().child(this.state.stallName).child(fromDB);
        rootRef2.on('child_added', snap => {
            if (snap.val()) {
                msgs.push({
                    uid: snap.val().uid,
                    time: snap.val().time,
                    name: snap.val().name,
                    msg: snap.val().text
                });
                tempArray.push(snap.val().uid);
                this.setState({ messages: msgs })
            }

        });


        rdbx.ref().child(this.state.stallName).child(fromDB).once('value', snapshot => {
            if (snapshot.exists()) {
                console.log('found');
            }
            else {
                const rootRef = rdbx.ref().child(this.state.stallName).child(fromDB);
                rootRef.push().set({
                    uid: person,
                    time: Date.now(),
                    name: this.state.stallName,
                    text: AppString.DEFULT_MESSAGE
                });
            }
        })
        //    console.log(givenDB);
        //     console.log(fromDB);
    }


    sortArrayElements = (msgs) => {
        for (let i = 0; i < msgs.length; i++) {
            for (let j = i + 1; j < msgs.length; j++) {
                if (msgs[i].time > msgs[j].time) {
                    let tmp = msgs[i];
                    msgs[i] = msgs[j];
                    msgs[j] = tmp;
                }
            }
        }
        return msgs;
    }

    getUserAvatarImageByID = (id, userType) => {
        const userRef = rdbx.ref('usersTable');
        let defaultUrl = AppString.DEFAULT_AVATAR;
        var snapThis = this;
        userRef.on('value', (snapshot) => {
            snapshot.forEach(function (child) {
                //  console.log(child.key);   
                if (id === child.key) {
                    defaultUrl = child.val().profile_picture;
                    if (userType === 0) {
                        snapThis.setState({ userAvatar: defaultUrl });
                    } else {
                        snapThis.setState({ stallManagerAvatar: defaultUrl });
                    }
                }
            })
        })

        if (userType === 0) {
            snapThis.setState({ userAvatar: defaultUrl });
        } else {
            this.setState({ stallManagerAvatar: defaultUrl });
        }

    }


    //Send Message
    pushMsg = (event) => {
        event.preventDefault();
        let uid = this.state.uid;
        let name = this.state.name;
        let text = this.state.text;
        let dbName = this.state.dbName;

        if (text.trim() === '') {
            return
        } else {
            //notification
            if (dbName !== 'chatRoom') {
                let uidArr = dbName.split('+');
                const removeNotification = rdbx.ref('notification').child(uid).child(uidArr[1]);
                const notification = rdbx.ref().child('notification').child(uidArr[1]);

                notification.child(uid).once('value', snapshot => {
                    if (snapshot.val()) {
                        notification.child(uid).set({
                            count: snapshot.val().count + 1
                        })
                    } else {
                        notification.child(uid).set({
                            count: 1
                        })
                    }
                })
                removeNotification.set({
                    count: 0
                })
            }

            const rootRef = rdbx.ref().child(this.state.stallName).child(dbName);
            rootRef.push().set({
                uid: uid,
                time: Date.now(),
                name: name,
                text: text
            });

            this.setState({ text: '' });
        }
        //console.log('message send');

    }

    //Scroll message UI-kit
    scrollToBottom = () => {
        const messagesContainer = ReactDOM.findDOMNode(this.messagesContainer);
        //console.log(messagesContainer.scrollHeight);
        messagesContainer.scroll(0, messagesContainer.scrollHeight);
    }
    // emoji open
    openEmoji = () => {
        let emoji = document.querySelector('.emoji');
        (emoji.style.display === 'block')
            ? emoji.style.display = "none"
            : emoji.style.display = "block"
    }
    openEmojiClose = () => {
        let emoji = document.querySelector('.emoji');
        if (emoji.style.display === 'block') { emoji.style.display = "none" }
    }

    pickEmoji = (emo) => {
        let prevText = this.state.text;
        this.setState({ text: prevText + emo })
    }


    //Sorting by time
    srotingMessageFromTime = (a, b) => {
        return a.time > b.time;
    }

    signout = () => {
        localStorage.setItem('loginKey', []);
        this.props.isLogout();
    }

    render() {
        return (
            <div className="main__container">
                <div className="chat_header">
                    <img src={this.state.stallManagerAvatar} alt="avatar" width="36" />

                    <div className="stallName">{this.state.stallExternalName}</div>
                    {/* <button className="signout" onClick={this.signout}> Signout </button> */}
                </div>
                <div className="wrapper">
                    <div className="right__section">

                        <div className="message__box" ref={(el) => { this.messagesContainer = el; }}>
                            {this.sortArrayElements(this.state.messages).map(
                                (message, i) => {
                                    if (message.uid === this.state.uid) {
                                        return (
                                            <div className="msg__text" key={i}>
                                                <div> <img src={this.state.userAvatar} alt="avatar" /> </div>
                                                <span>
                                                    <strong style={{ textTransform: 'capitalize', color: '#9E9E9E' }}>{message.name}</strong><br />
                                                    {message.msg}
                                                    <br />
                                                    <small style={{ color: '#9E9E9E' }}>{moment(message.time).fromNow()}</small>
                                                </span>
                                            </div>
                                        )
                                    } else {
                                        return (
                                            <div className="msg__text__right" key={i}>

                                                <div> <img src={this.state.stallManagerAvatar} alt="avatar" /> </div>
                                                <span>
                                                    <strong style={{ textTransform: 'capitalize', color: '#9E9E9E' }}>{message.name}</strong><br />
                                                    {message.msg}
                                                    <br />
                                                    <small style={{ color: '#9E9E9E' }}>{moment(message.time).fromNow()}</small>
                                                </span>
                                            </div>
                                        )
                                    }
                                }
                            )
                            }

                        </div>
                        <form>
                            <div className="message__type__box">
                                <input
                                    placeholder="Type Your Message"
                                    onChange={(e) => { this.setState({ text: e.target.value }) }}
                                    value={this.state.text}
                                    require="true"
                                    onFocus={this.openEmojiClose}
                                />
                                <span className="emojiIcon" onClick={this.openEmoji}> â˜º </span>
                                <button type="submit" onClick={this.pushMsg} > <i className="glyphicon glyphicon-send"></i> </button>
                            </div>
                        </form>
                    </div>
                </div>
                <div className="emoji">
                    {this.state.emoji.map(
                        (emo, i) =>
                            <a key={i} onClick={() => { this.pickEmoji(emo) }}> <span role="img">{emo}</span> </a>
                    )}
                </div>
            </div>
        )
    }
}

export default Home;
