import React, { Component } from 'react';
import './App.css';
import { authx, rdbx } from "./firebase";
import Home from "./component/home";
import { AppString } from "./const";


class App extends Component {

  state = {
    user: null
  };

  constructor(props) {
    super(props)
    this.state = {
      isLogin: false
    }
  }

  getUserDetails = (email) => {
    return new Promise(async (resolve, reject) => {
      try {
        let path = '/users'
        var dbref = rdbx.ref(path).orderByChild('email').equalTo(email);
        const query = await dbref.once("value")
        if (query.exists()) {
          let key = Object.keys(query.val())[0]
          resolve(query.val()[key])
        } else {
          reject({
            code: "NoUserFound",
            message: "No Such User Is Registered"
          })
        }
      } catch (err) {
        console.log(err)
        reject(err)
      }
    })
  }

  componentDidMount() {
    authx.onAuthStateChanged(async (user) => {
      if (user) {
        let urlParam = window.location.href.split('?')[1]
        if (urlParam) {
          AppString.STALLID = urlParam
        }
        console.log(AppString)
        // User is signed in.
        console.log(user.uid);
        //get company name
        const userData = await this.getUserDetails(user.email);
        if (userData) {
          if (userData.company) {
            user.company = userData.company;
          }
        }
        this.user_ActiveStatus(user.uid);
        this.setState({ user: { id: user.email, name: user.displayName } })

        //If already registred user
        let userRef = rdbx.ref().child(AppString.USERS_DOC).child(user.uid);

        userRef.once("value", snapshot => {

          var isAvailable = snapshot.val();

          if (!isAvailable) {
            this.addUserList(user);
          } else {
            //Check Default Image Is Their or not
            if (user.photoURL) {
              userRef.update({ profile_picture: user.photoURL });
            } else {
              userRef.update({ profile_picture: AppString.DEFAULT_AVATAR });
            }
          }
          console.log(AppString);

          localStorage.setItem(
            "loginKey",
            JSON.stringify({
              uid: user.uid,
              username: user.displayName,
              stallID: AppString.STALLID,
            })
          );
          this.isLogin();
          console.log('1st This');
        });

      } else {
        // No user is signed in.
        localStorage.removeItem("loginKey");
        console.log('No user is signed in');

        //User for test only 
        this.loginWithGmail('shubham@dj.com', 'shubham@dj.com123456');
        //User for test only 
      }
    });
  }

  user_ActiveStatus = (current_user_uid) => {

    let uid = current_user_uid;

    let userStatusDatabaseRef = rdbx.ref(AppString.STALL_ONLINE + uid + '/State/');

    let isOfflineForDatabase = {
      StateMode: 'offline',
    };

    let isOnlineForDatabase = {
      StateMode: 'online',
    };

    rdbx.ref('.info/connected').on('value', function (snapshot) {
      if (snapshot.val() == false) {
        return;
      };
      userStatusDatabaseRef.onDisconnect().set(isOfflineForDatabase).then(function () {
        userStatusDatabaseRef.set(isOnlineForDatabase);
      });
    });
  }

  addUserList = user => {
    const rootRef = rdbx.ref().child(AppString.USERS_DOC).child(user.uid);

    let userImage = '';
    if (user.photoURL) {
      userImage = user.photoURL;
    } else {
      userImage = AppString.DEFAULT_AVATAR;
    }

    if (user.company) {
      rootRef.set({
        userName: user.displayName ? user.displayName : user.email.split('@')[0],
        profile_picture: userImage,
        fullName: user.displayName ? user.displayName : user.email.split('@')[0],
        email: user.email,
        company: user.company,
      });
    } else {
      rootRef.set({
        userName: user.displayName ? user.displayName : user.email.split('@')[0],
        profile_picture: userImage,
        fullName: user.displayName ? user.displayName : user.email.split('@')[0],
        email: user.email
      });
    }


  };

  isLogin = () => {
    this.setState({ isLogin: true })
  }
  isLogout = () => {
    this.setState({ isLogin: false })
  }

  //Only For Text=======
  loginWithGmail = (email, password) => {
    authx.signInWithEmailAndPassword(email, password).then(result => {
      console.log("Don't user this only for testing use this login");
    }).catch(error => {
      var errorMessage = error.message;
      console.log(errorMessage);
    });
  };
  //================


  render() {
    return (
      <>
        {(this.state.isLogin)
          ? <Home isLogout={this.isLogout} />
          : null
        }
      </>
    );
  }
}

export default App;
